import React from "react";
import { RiUserReceivedFill } from "react-icons/ri";
import { TransferInfoType } from "../../types/userInfo";
import { DATE } from "../../utils/functions";

type UserTransferProps = {
  userTransfers: TransferInfoType[];
};

const UserTransfer: React.FC<UserTransferProps> = (props) => {
  return (
    <div>
      <div className="flex items-center space-x-2 font-semibold text-gray-900 leading-8 mb-3">
        <span className="text-primary-800">
          <RiUserReceivedFill size={25} />
        </span>
        <span className="tracking-wide">Transfer</span>
      </div>
      <ul className="list-inside space-y-2 divide-y-2">
        {props.userTransfers.length === 0 && (
          <li>
            <div className="text-teal-600 text-gray-500">
              No transfer available
            </div>
          </li>
        )}
        {props.userTransfers.map((transfer, index) => (
          <li key={index + Math.random()}>
            <div className="text-teal-600">
              Transferred from:{" "}
              <span className="font-semibold">{transfer.from_school_name}</span>
            </div>
            <div className="text-teal-600">
              Transferred to:{" "}
              <span className="font-semibold">
                {transfer.to_school_name ? transfer.to_school_name : ""}
              </span>
            </div>
            <div className="text-gray-500 text-sm">
              Transferred on:{" "}
              <span className="font-semibold">
                {transfer.transfer_date ? DATE(transfer.transfer_date) : "-"}
              </span>
            </div>
            <div className="text-gray-500 text-xs">{transfer.transfer_id}</div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default UserTransfer;
