import React from "react";
import { MdBorderColor, MdFilePresent } from "react-icons/md";
import { UserEducationType } from "../../types/userInfo";
import { DATE } from "../../utils/functions";

type UserEducationProps = {
  userEducations: UserEducationType[];
};

const UserEducation: React.FC<UserEducationProps> = (props) => {
  return (
    <div>
      <div className="flex items-center space-x-2 font-semibold text-gray-900 leading-8 mb-3">
        <span className="text-primary-800">
          <MdBorderColor size={25} />
        </span>
        <span className="tracking-wide">Education</span>
      </div>
      <ul className="list-inside space-y-2 divide-y-2">
        {props.userEducations.length === 0 && (
          <li>
            <div className="text-teal-600 text-gray-500">
              No education available
            </div>
          </li>
        )}
        {props.userEducations.map((education, index) => (
          <li key={index + Math.random()}>
            <div className="text-teal-600 flex items-center gap-2">
              <a
                href={
                  education.qualification_document
                    ? education.qualification_document
                    : ""
                }
                target="_blank"
                rel="noopener noreferrer"
              >
                {education.qualification_name}
                {education.given_institution &&
                  " in " + education.given_institution}
              </a>

              <a
                href={
                  education.qualification_document
                    ? education.qualification_document
                    : ""
                }
                target="_blank"
                rel="noopener noreferrer"
              >
                <MdFilePresent className="text-gray-700 text-xl" />
              </a>
            </div>
            <div className="text-gray-500 text-xs">
              {education.qualification_issued_date
                ? DATE(education.qualification_issued_date)
                : "-"}
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default UserEducation;
