import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { Auth, Basic, FC_GetAllTermination, TeacherData } from "../../actions";
import { StoreState } from "../../reducers";

import Alert from "../../components/Alert/Alert";
import BasicInfo from "./BasicInfo";
import { ProfileType } from "../../types/userInfo";
import { FC_GetProfile } from "../../actions/profile.action";
import { isEmptyOrSpaces } from "../../utils/functions";
import WorkingInfo from "./WorkingInfo";
import UserExperiences from "./UserExperiences";
import UserEducation from "./UserEducation";
import UserTermination from "./UserTermination";
import UserSuspension from "./UserSunspension";
import UserTransfer from "./UserTransfer";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import Button from "../../components/Buttons/Button";
import { APITerminationType } from "../../types/StaffTermination";
// import ViewTermination from "../HTTermination_old/ViewTermination";
// import HTTerminationForm from "../HTTermination_old/HTTerminationForm";
import { TerminationReducerType } from "../../reducers/termination.reducer";
// import HTSuspensionForm from "../HTTermination_old/HTSuspensionForm";
import ViewTermination from "../HTTermination/ViewTermination";
import HTTerminationForm from "../HTTermination/HTTerminationForm";
import HTSuspensionForm from "../HTTermination/HTSuspensionForm";
import Loading from "../../components/Loading/Loading";
import { TeacherStudyHierarchy } from "../../components/TeacherStudyHierarchy/TeacherStudyHierarchy";
import { MdInfoOutline } from "react-icons/md";
import { AssignStaffToSchool } from "./AssignStaffToSchool";
import { forReportRoles } from "../../types/PlacementReportTypes";

const getTerminationByUserId = (
  allTerminate: APITerminationType[],
  user_code: string
) => {
  console.log("T: ", allTerminate);
  return allTerminate.find((item) => item.teacher_id === user_code);
};

type UserProfileProps = {
  user_id?: string;
  isExternal?: boolean;
  close?: (status: any) => void;
  // NEW PROPS ===================
  setEditStaff?: (value: string) => void;
  user_data?: TeacherData;
  setAddUserPositions?: (value: TeacherData) => void;
  DisableUser?: (value: string) => void;

  // redux ===========
  auth: Auth;
  basic: Basic;
  terminations: TerminationReducerType;
  FC_GetAllTermination: Function;
};

type UserProfileState = {
  openSuspensionModal: boolean;
  operationActionType: "suspend" | "terminate";
  suspendStartDate: string;
  suspendEndDate: string;
  title: string;
  userInfo: ProfileType | null;
  comment: string;
  error: {
    target: "main-error" | "main-success" | null;
    msg: string;
  };
  loading: boolean;
  thisTermination: APITerminationType | null;
  action: "TERMINATION" | "SUSPENSION" | null;
  assign_to_school: boolean;
};

class _UserProfile extends Component<UserProfileProps, UserProfileState> {
  constructor(props: UserProfileProps) {
    super(props);
    this.state = {
      openSuspensionModal: false,
      operationActionType: "suspend",
      suspendStartDate: "",
      suspendEndDate: "",
      title: "",
      userInfo: null,
      comment: "",
      error: {
        target: null,
        msg: "",
      },
      loading: true,
      thisTermination: null,
      action: null,
      assign_to_school: false,
    };
  }

  setThisTermination = (value: APITerminationType | null) => {
    this.setState({ thisTermination: value });
  };

  setUserInfo = (value: ProfileType | null) => {
    this.setState({ userInfo: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setError = (value: {
    target: "main-error" | "main-success" | null;
    msg: string;
  }) => {
    this.setState({ error: value });
  };

  setAction = (value: "TERMINATION" | "SUSPENSION" | null) => {
    this.setState({ action: value });
  };

  componentDidMount = () => {
    const user_id: string = !this.props.isExternal
      ? this.props.auth.user
        ? this.props.auth.user.user_id
        : ""
      : this.props.user_id
      ? this.props.user_id
      : "";
    FC_GetProfile(
      user_id,
      (status: boolean, data: ProfileType | null, msg: string) => {
        this.setLoading(status);
        if (!status && !isEmptyOrSpaces(msg))
          this.setError({
            target: "main-error",
            msg: msg,
          });
        else if (!status && data) this.setUserInfo(data);
        console.log("=================FC_GetProfile===================");
        console.log({ FC_GetProfile: data });
        console.log("=================FC_GetProfile===================");
      }
    );

    this.props.FC_GetAllTermination(
      "hd",
      (status: boolean, data: APITerminationType[] | null, msg: string) => {
        console.log("================FC_GetAllTermination====================");
        console.log({ FC_GetAllTermination: data });
        console.log("================FC_GetAllTermination====================");
        this.setLoading(status);
        if (!status && !isEmptyOrSpaces(msg))
          this.setError({
            target: "main-error",
            msg: msg,
          });
      }
    );
  };

  render() {
    console.log("==================buttonssssss==================");
    console.log({ user_data: this.props.user_data });
    console.log("==================buttonssssss==================");

    if (
      (!this.props.user_id || isEmptyOrSpaces(this.props.user_id)) &&
      this.props.isExternal === true
    )
      <div className="py-10 px-10">
        <div className="bg-yellow-50 text-yellow-900 border border-yellow-900 rounded flex flex-col items-center justify-center p-6 my-8">
          <MdInfoOutline className="text-6xl md:text-9xl" />
          <div className="text-center flex flex-col">
            <span className="text-2xl md:text-4xl font-extrabold">
              User id not provided!
            </span>
            <span className="text-lg">
              Please try again there must be an error, or invalid staff code!
            </span>

            {this.props.isExternal && (
              <Button
                theme="primary-light"
                className="mt-4"
                onClick={this.props.close!}
              >
                Back
              </Button>
            )}
          </div>
        </div>
      </div>;

    if (
      this.state.assign_to_school === true &&
      this.props.user_id !== undefined
    )
      return (
        <AssignStaffToSchool
          user_id={this.props.user_id}
          // userInfo={this.state.userInfo}
          userDetails={{
            email: this.state.userInfo?.staffDetails.basicInfo.email,
            full_name: this.state.userInfo?.staffDetails.basicInfo.full_name,
            nid: this.state.userInfo?.staffDetails.basicInfo.nid,
            staff_code: this.state.userInfo?.staffDetails.basicInfo.staff_code,
          }}
          close={() =>
            this.setState({
              assign_to_school: false,
            })
          }
          district_code={
            this.state.userInfo?.staffDetails.workInfo?.school_district
          }
          // current_position={
          //   this.state.userInfo?.staffDetails.workInfo.
          // }
          // district_name={this.state.userInfo?.staffDetails.workInfo?.}
          school_code={this.state.userInfo?.staffDetails.workInfo?.school_code}
          school_name={this.state.userInfo?.staffDetails.workInfo?.school_name}
        />
      );

    if (this.state.loading)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (
      this.state.userInfo !== null &&
      (!this.state.userInfo.staffDetails ||
        !this.state.userInfo.staffDetails.basicInfo)
    )
      return (
        <div className="py-10 px-10">
          <div className="bg-yellow-50 text-yellow-900 border border-yellow-900 rounded flex flex-col items-center justify-center p-6 my-8">
            <MdInfoOutline className="text-6xl md:text-9xl" />
            <div className="text-center flex flex-col">
              <span className="text-2xl md:text-4xl font-extrabold">
                Profile not found!
              </span>
              <span className="text-lg">
                Please try again there must be an error, or invalid staff code!
              </span>

              {this.props.isExternal && (
                <Button
                  theme="primary-light"
                  className="mt-4"
                  onClick={this.props.close!}
                >
                  Back
                </Button>
              )}
            </div>
          </div>
        </div>
      );

    if (this.state.thisTermination && !this.state.action && this.state.userInfo)
      return (
        <ViewTermination
          selectedStaff={this.state.userInfo.staffDetails.basicInfo}
          close={(e) => this.setThisTermination(e)}
          thiTermination={this.state.thisTermination}
        />
      );

    if (
      this.state.userInfo &&
      this.state.action &&
      this.state.action === "TERMINATION"
    )
      return (
        <HTTerminationForm
          selectedStaff={this.state.userInfo.staffDetails.basicInfo}
          auth={this.props.auth}
          close={() => {
            this.setAction(null);
          }}
        />
      );

    if (
      this.state.userInfo &&
      this.state.action &&
      this.state.action === "SUSPENSION"
    )
      return (
        <HTSuspensionForm
          selectedStaff={this.state.userInfo.staffDetails.basicInfo}
          auth={this.props.auth}
          close={() => {
            this.setAction(null);
          }}
        />
      );

    console.log({ user_id: this.props.user_id });

    return (
      <>
        {this.state.userInfo && (
          <DashboardContainer>
            <div className="flex items-center gap-3 justify-between">
              <div className="flex-1">
                <Heading>
                  {this.props.isExternal
                    ? "Staff Information"
                    : "Profile Information"}
                </Heading>
              </div>

              <div className="flex items-center gap-2">
                {/* Buttons */}
                {/* {this.props.auth.role &&
                  this.props.isExternal &&
                  (forReportRoles.district.includes(
                    this.props.auth.role.role
                  ) ||
                    forReportRoles.school.includes(
                      this.props.auth.role.role
                    )) && ( */}
                <Button
                  onClick={() => {
                    this.setState({
                      assign_to_school: true,
                    });
                  }}
                >
                  Change School/ Position
                </Button>
                {/* )} */}

                {/* {this.props.auth.role &&
                  this.props.isExternal &&
                  forReportRoles.school.includes(this.props.auth.role.role) &&
                  this.props.terminations.allTermination &&
                  getTerminationByUserId(
                    this.props.terminations.allTermination,
                    this.state.userInfo.staffDetails.basicInfo.staff_code
                  ) && (
                    <Button
                      onClick={() => {
                        this.setThisTermination(
                          getTerminationByUserId(
                            this.props.terminations.allTermination!,
                            this.state.userInfo!.staffDetails.basicInfo
                              .staff_code
                          )!
                        );
                      }}
                    >
                      View
                    </Button>
                  )} */}
                {/* {this.props.auth.role &&
                  this.props.isExternal &&
                  forReportRoles.school.includes(this.props.auth.role.role) &&
                  this.props.terminations.allTermination &&
                  !getTerminationByUserId(
                    this.props.terminations.allTermination,
                    this.state.userInfo.staffDetails.basicInfo.staff_code
                  ) && (
                    <Button onClick={() => this.setAction("SUSPENSION")}>
                      Suspend
                    </Button>
                  )} */}
                {/* {this.props.auth.role &&
                  this.props.isExternal &&
                  forReportRoles.school.includes(this.props.auth.role.role) &&
                  this.props.terminations.allTermination &&
                  !getTerminationByUserId(
                    this.props.terminations.allTermination,
                    this.state.userInfo.staffDetails.basicInfo.staff_code
                  ) && (
                    <Button
                      theme="danger"
                      className="bg-red-800 p-2 hover:bg-red-900"
                      onClick={() => this.setAction("TERMINATION")}
                    >
                      Terminate
                    </Button>
                  )} */}
                {this.props.auth.role && this.props.isExternal && (
                  <div className="dropdown inline-block relative">
                    <button className="opacity-80 group-hover:opacity-100 bg-accent-800 bg-opacity-0 hover:bg-opacity-10 text-accent-900 font-semibold py-2 px-4 rounded inline-flex items-center">
                      <span className="mr-1">Manage</span>
                      <svg
                        className="fill-current h-4 w-4"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 20"
                      >
                        <path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" />{" "}
                      </svg>
                    </button>
                    <ul className="dropdown-menu absolute hidden bg-white shadow text-gray-700 z-50 p-2 rounded">
                      {forReportRoles.school.includes(
                        this.props.auth.role.role
                      ) &&
                        this.props.user_id &&
                        !isEmptyOrSpaces(this.props.user_id) &&
                        this.props.setEditStaff &&
                        this.props.setAddUserPositions &&
                        this.props.DisableUser &&
                        this.props.user_data && (
                          <>
                            <li className="p-0.5">
                              <button
                                onClick={() =>
                                  this.props.user_id &&
                                  this.props.setEditStaff!(this.props.user_id)
                                }
                                className="rounded block bg-gray-500 hover:bg-gray-800 text-white py-2 px-4 w-full whitespace-no-wrap"
                              >
                                Edit
                              </button>
                            </li>
                            <li className="p-0.5">
                              <button
                                onClick={() =>
                                  this.props.setAddUserPositions!(
                                    this.props.user_data!
                                  )
                                }
                                className="rounded block bg-blue-200 hover:bg-gray-800 text-blue-900 hover:text-white py-2 px-4 w-full whitespace-no-wrap"
                              >
                                Position
                              </button>
                            </li>
                            <li className="p-0.5">
                              <button
                                className="rounded block bg-red-500 hover:bg-red-800 text-white py-2 px-4 w-full whitespace-no-wrap"
                                onClick={() =>
                                  this.props.DisableUser!(this.props.user_id!)
                                }
                              >
                                Remove
                              </button>
                            </li>
                          </>
                        )}
                      {this.props.auth.role &&
                        this.props.isExternal &&
                        forReportRoles.school.includes(
                          this.props.auth.role.role
                        ) &&
                        this.props.terminations.allTermination &&
                        !getTerminationByUserId(
                          this.props.terminations.allTermination,
                          this.state.userInfo.staffDetails.basicInfo.staff_code
                        ) && (
                          <li className="p-0.5">
                            <Button
                              className="p-2"
                              onClick={() => this.setAction("SUSPENSION")}
                            >
                              Suspend
                            </Button>
                          </li>
                        )}
                      {this.props.auth.role &&
                        this.props.isExternal &&
                        forReportRoles.school.includes(
                          this.props.auth.role.role
                        ) &&
                        this.props.terminations.allTermination &&
                        !getTerminationByUserId(
                          this.props.terminations.allTermination,
                          this.state.userInfo.staffDetails.basicInfo.staff_code
                        ) && (
                          <li className="p-0.5">
                            <Button
                              theme="danger"
                              className="bg-red-800 p-2 hover:bg-red-900"
                              onClick={() => this.setAction("TERMINATION")}
                            >
                              Terminate
                            </Button>
                          </li>
                        )}
                      {this.props.auth.role &&
                        this.props.isExternal &&
                        forReportRoles.school.includes(
                          this.props.auth.role.role
                        ) &&
                        this.props.terminations.allTermination &&
                        getTerminationByUserId(
                          this.props.terminations.allTermination,
                          this.state.userInfo.staffDetails.basicInfo.staff_code
                        ) && (
                          <li className="p-0.5">
                            <Button
                              onClick={() => {
                                this.setThisTermination(
                                  getTerminationByUserId(
                                    this.props.terminations.allTermination!,
                                    this.state.userInfo!.staffDetails.basicInfo
                                      .staff_code
                                  )!
                                );
                              }}
                            >
                              View request
                            </Button>
                          </li>
                        )}
                    </ul>
                  </div>
                )}
                {this.props.close && (
                  <div className="">
                    <Button onClick={this.props.close!} theme="default">
                      Close
                    </Button>
                  </div>
                )}
              </div>
            </div>
            {this.state.error.target === "main-success" && (
              <Alert theme="success" fixed>
                {this.state.error.msg}
              </Alert>
            )}
            {this.state.error.target === "main-error" && (
              <Alert theme="danger" fixed>
                {this.state.error.msg}
              </Alert>
            )}
            <div className="bg-gray-100">
              <div className="container mx-auto my-5 p-5">
                <div className="md:flex no-wrap md:-mx-2 ">
                  {/* <!-- Left Side --> */}
                  <WorkingInfo
                    basicInfo={this.state.userInfo.staffDetails.basicInfo}
                    workInfo={this.state.userInfo.staffDetails.workInfo}
                  />

                  {/* <!-- Right Side --> */}
                  <div className="w-full md:w-9/12 mx-2">
                    {/* <!-- Profile tab --> */}
                    {/* <!-- About Section --> */}
                    <BasicInfo
                      info={this.state.userInfo.staffDetails.basicInfo}
                    />
                    {/* <!-- End of about section --> */}

                    <div className="my-4"></div>

                    {/* <!-- Experience and education --> */}
                    <div className="bg-white p-3 shadow-sm rounded-sm">
                      <div className="grid grid-cols-2">
                        {/* <!-- Education --> */}
                        <UserEducation
                          userEducations={
                            this.state.userInfo.staffDetails.education
                              ? this.state.userInfo.staffDetails.education
                              : []
                          }
                        />
                        {/* <!-- End Education --> */}

                        {/* <!-- Experience --> */}
                        <UserExperiences
                          userExperiences={
                            this.state.userInfo.staffDetails.experience
                              ? this.state.userInfo.staffDetails.experience
                              : []
                          }
                        />
                        {/* <!-- End Experience --> */}
                      </div>
                      {/* <!-- End of Experience and education grid --> */}
                    </div>
                    {/* <!-- End of profile tab --> */}

                    <div className="my-4"></div>

                    {/* <!-- termination && suspension --> */}
                    <div className="bg-white p-3 shadow-sm rounded-sm">
                      <div className="grid grid-cols-2">
                        {/* <!-- termination --> */}
                        <UserTermination
                          userTerminations={
                            this.state.userInfo.staffDetails.terminationInfo
                              ? this.state.userInfo.staffDetails.terminationInfo
                              : []
                          }
                        />
                        {/* <!-- End termination --> */}

                        {/* <!-- Suspension --> */}
                        <UserSuspension
                          userSuspensions={
                            this.state.userInfo.staffDetails.suspensionInfo
                              ? this.state.userInfo.staffDetails.suspensionInfo
                              : []
                          }
                        />
                        {/* <!-- End Suspension --> */}
                      </div>
                      {/* <!-- End of termination && suspension grid --> */}
                    </div>
                    {/* <!-- End of profile tab --> */}

                    <div className="my-4"></div>

                    {/* <!-- transfer --> */}
                    <div className="bg-white p-3 shadow-sm rounded-sm">
                      <div className="grid grid-cols-2">
                        {/* <!-- transfer --> */}
                        <UserTransfer
                          userTransfers={
                            this.state.userInfo.staffDetails.transferInfo
                              ? this.state.userInfo.staffDetails.transferInfo
                              : []
                          }
                        />
                        {/* <!-- End transfer --> */}
                      </div>
                      {/* <!-- End of transfer grid --> */}
                    </div>
                    {/* <!-- End of profile tab --> */}
                  </div>
                </div>
              </div>
            </div>
            {
              <TeacherStudyHierarchy
                allowEdit={this.props.isExternal}
                staff_code={
                  this.state.userInfo.staffDetails.basicInfo.staff_code
                }
                full_name={this.state.userInfo.staffDetails.basicInfo.full_name}
              />
            }
          </DashboardContainer>
        )}
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  terminations,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  terminations: TerminationReducerType;
} => {
  return { auth: auth, basic: basic, terminations };
};

export const UserProfile = connect(mapStateToProps, { FC_GetAllTermination })(
  _UserProfile
);
