import React from "react";
import { MdFilePresent } from "react-icons/md";
import { UserExperienceType } from "../../types/userInfo";
import { DATE } from "../../utils/functions";

type UserExperiencesProps = {
  userExperiences: UserExperienceType[];
};

const UserExperiences: React.FC<UserExperiencesProps> = (props) => {
  return (
    <div>
      <div className="flex items-center space-x-2 font-semibold text-gray-900 leading-8 mb-3">
        <span className="text-primary-800">
          <MdFilePresent size={25} />
        </span>
        <span className="tracking-wide">Experience</span>
      </div>
      <ul className="list-inside space-y-2 divide-y-2">
        {props.userExperiences.length === 0 && (
          <li>
            <div className="text-teal-600 text-gray-500">
              No experience available
            </div>
          </li>
        )}
        {props.userExperiences.map((experience, index) => (
          <li key={index + Math.random()}>
            <div className="text-teal-600">
              {experience.position} at {experience.institution}
            </div>
            <div className="text-gray-500 text-xs">
              {experience.starting_date
                ? DATE(experience.starting_date) +
                  " - " +
                  (experience.ending_date
                    ? DATE(experience.ending_date)
                    : "Now")
                : "-"}{" "}
              - {experience.ending_date}
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default UserExperiences;
