import React from "react";
import { RiUserUnfollowFill } from "react-icons/ri";
import { SuspensionInfoType } from "../../types/userInfo";
import { DATE } from "../../utils/functions";

type UserSuspensionProps = {
  userSuspensions: SuspensionInfoType[];
};

const UserSuspension: React.FC<UserSuspensionProps> = (props) => {
  return (
    <div>
      <div className="flex items-center space-x-2 font-semibold text-gray-900 leading-8 mb-3">
        <span className="text-primary-800">
          <RiUserUnfollowFill size={25} />
        </span>
        <span className="tracking-wide">Suspensions</span>
      </div>
      <ul className="list-inside space-y-2 divide-y-2">
        {props.userSuspensions.length === 0 && (
          <li>
            <div className="text-teal-600 text-gray-500">
              No Suspension available
            </div>
          </li>
        )}
        {props.userSuspensions.map((suspension, index) => (
          <li key={index + Math.random()}>
            <div className="text-teal-600">
              Suspended on:{" "}
              <span className="font-semibold">
                {suspension.suspended_from_school}
              </span>
            </div>
            <div className="text-teal-600">
              Start date:{" "}
              <span className="font-semibold">
                {DATE(suspension.suspended_from)}
              </span>
            </div>
            <div className="text-teal-600">
              End date:{" "}
              <span className="font-semibold">
                {DATE(suspension.suspended_to)}
              </span>
            </div>
            <div className="text-gray-500 text-sm">
              {suspension.suspended_reason}
            </div>
            <div className="text-gray-500 text-xs">
              {suspension.suspended_status}
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default UserSuspension;
