import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { Auth, Basic, SchoolHierarchyStore } from "../../actions";
import { StoreState } from "../../reducers";
import { ProfileType } from "../../types/userInfo";

import { IoIosArrowDown } from "react-icons/io";
import { MdClose } from "react-icons/md";
import { GrPowerReset } from "react-icons/gr";
import {
  FC_GetSchoolHierarchy,
  FC_GetSchoolLevels,
  FilteredCombination,
  HierarchyDataType,
  SchoolHierarchyGet,
  TeacherHierarchyGet,
  SchoolHierarchyInterface,
  FC_AllSchoolHierarchyDetails,
  FC_GetTeacherSchoolHierarchy,
} from "../../actions";
import { search } from "../../utils/functions";
import Loading from "../Loading/Loading";
import AddNew from "./AddNewTeacherStudyHierarchy";
import AddNewTeacherStudyHierarchy from "./AddNewTeacherStudyHierarchy";
import axios from "axios";
import { APP_URL } from "../../config/app.config";
import { errorToText } from "../../utils/errors";

interface StudyHierarchyProps {
  schoolHierarchyStore: SchoolHierarchyStore;
  auth: Auth;
  basic: Basic;
  FC_AllSchoolHierarchyDetails: (
    callBack: (status: boolean, msg: string) => void
  ) => void;
  staff_code: string;
  full_name: string;
  allowEdit?: boolean | undefined;
}
interface StudyHierarchyState {
  loading: boolean;
  school_hierarchy: SchoolHierarchyGet[] | null;
  teacher_hierarchy_data: SchoolHierarchyInterface[] | null;
  school_hierarchy_data: SchoolHierarchyInterface[] | null;
  selected_level_code: string | null;
  selected_combination_code: string | null;
  filter_by_combination: boolean;
  search_comb: string;
  addNew: boolean;
  addingNew: boolean;
  teacherSchoolHierarchy: TeacherHierarchyGet[];
  FilteredData: SchoolHierarchyInterface[] | null;
  success: string;
  error: string;
}

export const GetLoading = (): JSX.Element => {
  return (
    <div className="bg-white rounded-md p-4 flex flex-col items-center justify-center m-5">
      <Loading />
      <div className="text-xl">Loading, please wait...</div>
    </div>
  );
};

export class _TeacherStudyHierarchy extends Component<
  StudyHierarchyProps,
  StudyHierarchyState
> {
  constructor(props: StudyHierarchyProps) {
    super(props);

    this.state = {
      loading: true,
      school_hierarchy: null,
      teacher_hierarchy_data: null,
      selected_level_code: null,
      selected_combination_code: null,
      filter_by_combination: false,
      search_comb: "",
      addNew: false,
      addingNew: false,
      teacherSchoolHierarchy: [],
      school_hierarchy_data: null,
      FilteredData: null,
      success: "",
      error: "",
    };
  }
  setAddNew = (status: boolean) => {
    this.setState({
      addNew: status,
      addingNew: status !== true ? false : true,
    });
  };
  setAddingNew = (status: boolean) => {
    this.setState({ addingNew: status });
  };
  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  FilterSchoolHierarchy = (
    data: SchoolHierarchyGet[]
  ): SchoolHierarchyInterface[] => {
    let response: SchoolHierarchyInterface[] = [];
    for (let item of data) {
      let test =
        this.props.schoolHierarchyStore.hierarchy === null
          ? undefined
          : this.props.schoolHierarchyStore.hierarchy.find(
              (itm) => itm.studyhierarchyid === item.shool_hierarch_id
            );
      if (test !== undefined) {
        response.push(test);
      }
    }
    return response;
  };
  FilterTeacherHierarchy = (
    data: TeacherHierarchyGet[]
  ): SchoolHierarchyInterface[] => {
    let response: SchoolHierarchyInterface[] = [];
    for (let item of data) {
      let test =
        this.props.schoolHierarchyStore.hierarchy === null
          ? undefined
          : this.props.schoolHierarchyStore.hierarchy.find(
              (itm) =>
                itm.studyhierarchyid === item.study_hierarchy_id &&
                item.status !== "0"
            );
      if (test !== undefined) {
        response.push(test);
      }
    }
    return response;
  };
  setLoadSchoolSubjects = () => {
    if (this.props.auth.school !== null)
      return FC_GetSchoolHierarchy(
        this.props.auth.school.school_code,
        (status: boolean, response: SchoolHierarchyGet[] | null) => {
          this.setLoading(true);
          if (status === true && response !== null) {
            this.setState({
              school_hierarchy: response,
              school_hierarchy_data: this.FilterSchoolHierarchy(response),
              loading: false,
            });
          } else {
            this.setState({
              school_hierarchy: [],
              school_hierarchy_data: [],
              loading: false,
            });
          }
        }
      );
  };
  setLoadTeacherSubjects = () => {
    return FC_GetTeacherSchoolHierarchy(
      this.props.staff_code,
      (status: boolean, response: TeacherHierarchyGet[] | null) => {
        this.setLoading(true);
        if (status === true && response !== null) {
          console.log("Teacher hie: ", response);
          this.setState({
            teacher_hierarchy_data: this.FilterTeacherHierarchy(response),
            teacherSchoolHierarchy: response,
            loading: false,
          });
          this.FilteredData(this.FilterTeacherHierarchy(response));
        } else {
          this.setState({
            teacher_hierarchy_data: [],
            teacherSchoolHierarchy: [],
            loading: false,
          });
          this.FilteredData([]);
        }
      }
    );
  };
  componentDidMount = () => {
    if (this.props.schoolHierarchyStore.hierarchy === null) {
      this.setLoading(true);
      this.props.FC_AllSchoolHierarchyDetails(
        (status: boolean, msg: string) => {
          if (status === true) {
            this.setLoading(false);
          }
        }
      );
    }
    this.setLoadSchoolSubjects();
    this.setLoadTeacherSubjects();
  };

  ReturnGradeGroups = (item: SchoolHierarchyInterface): string[] => {
    let test = this.state.teacherSchoolHierarchy.find(
      (itm) => itm.study_hierarchy_id === item.studyhierarchyid
    )?.grade_group;
    if (test !== undefined) {
      return JSON.parse(test);
    }
    return [];
  };

  FilteredData = (res: SchoolHierarchyInterface[]) => {
    try {
      console.log("Testing user hierarchy: ", res);
      if (
        this.state.selected_level_code !== null &&
        this.state.selected_level_code !== ""
      ) {
        res = res.filter(
          (itm) => itm.level_code === this.state.selected_level_code
        );
      }
      if (this.state.selected_combination_code !== null) {
        res = res.filter(
          (itm) => itm.combination_code === this.state.selected_combination_code
        );
      }
      this.setState({ FilteredData: res });
    } catch (error: any) {
      console.log("Error of loading: ", { ...error });
    }
  };

  TotalPeriods = (): number => {
    let total = 0;
    // For the existing
    if (
      this.state.teacher_hierarchy_data !== null &&
      this.state.teacher_hierarchy_data.length > 0
    ) {
      for (let item of this.state.teacher_hierarchy_data) {
        total +=
          parseInt(item.teaching_period) * this.ReturnGradeGroups(item).length;
      }
    }
    return total;
  };

  RemoveTeacherSchoolHierarchy = async (id: string) => {
    if (
      window.confirm(
        "Are you sure do you want to remove this subject on school?"
      ) === true
    ) {
      this.setLoading(true);
      try {
        const res = await axios.post(
          `${APP_URL}/teacherhierarch/delete/${id}`,
          {}
        );
        console.log("Success: ", res);
        if (res.status === 200) {
          this.setLoadTeacherSubjects();
          this.setState({
            error: "",
            success: "School subject removed successfully!",
          });
        }
      } catch (error) {
        this.setState({
          error: errorToText(error),
          success: "",
          loading: false,
        });
      }
    }
  };

  render() {
    if (
      this.state.school_hierarchy === null ||
      this.props.schoolHierarchyStore.hierarchy === null ||
      this.state.loading === true ||
      this.state.teacher_hierarchy_data === null ||
      this.state.school_hierarchy_data === null
    ) {
      <GetLoading />;
    }
    return (
      <Fragment>
        {console.log("Teacher subjects: ", this.state.teacher_hierarchy_data)}
        <div className="mb-10 bg-white rounded m-2">
          <div>
            <div className="p-2 mb-5">
              <div className="flex flex-row items-center gap-3 justify-between">
                <div className="flex flex-col">
                  <div className="text-2xl font-bold">
                    TEACHER SUBJECTS ASSIGNMENT
                  </div>
                </div>
                <div>
                  <div
                    className={`flex flex-col ${
                      this.TotalPeriods() > 45
                        ? "bg-red-100 text-red-800"
                        : "bg-blue-100 text-primary-800"
                    }  rounded-t-md p-1 px-2 text-center`}
                  >
                    <span className="text-sm">
                      Total periods{" "}
                      {this.TotalPeriods() > 45 ? "(overloaded)" : ""}
                    </span>
                    <span className="text-xl font-bold -mt-1">
                      {this.TotalPeriods()}
                    </span>
                  </div>
                  {this.props.allowEdit && (
                    <div
                      onClick={() => {
                        this.setAddingNew(true);
                        this.setAddNew(true);
                      }}
                      className={`border font-bold ${
                        this.state.addingNew === false
                          ? "bg-yellow-100 border-yellow-500 hover:border-yellow-600"
                          : "bg-gray-100"
                      } text-black px-3 py-2 rounded text-center hover:bg-accent-900 hover:text-white cursor-pointer text-base`}
                    >
                      {this.state.addingNew === true
                        ? "Loading"
                        : this.state.teacher_hierarchy_data !== null &&
                          this.state.teacher_hierarchy_data.length > 0
                        ? "Add & Update Subjects"
                        : "Assign teacher subjects"}
                    </div>
                  )}
                </div>
              </div>
              {this.state.success !== "" && (
                <div
                  onClick={() => this.setState({ success: "" })}
                  className="bg-green-600 text-white font-bold pl-4 pr-2 py-2 text-lg rounded-md my-2 fixed right-10 cursor-pointer shadow-xl animate__animated animate__bounceInRight"
                >
                  <div className="flex flex-row justify-between items-center gap-8">
                    <div>Success: {this.state.success}</div>
                    <div>
                      <MdClose className="text-2xl text-red-600 bg-red-100 rounded-full" />
                    </div>
                  </div>
                </div>
              )}
              {this.state.error !== "" && (
                <div
                  onClick={() => this.setState({ error: "" })}
                  className="bg-red-600 text-white font-bold pl-4 pr-2 py-2 text-lg rounded-md my-2 fixed right-10 cursor-pointer shadow-xl animate__animated animate__bounceInRight"
                >
                  <div className="flex flex-row justify-between items-center gap-8">
                    <div>Error: {this.state.error}</div>
                    <div>
                      <MdClose className="text-2xl text-red-600 bg-red-100 rounded-full" />
                    </div>
                  </div>
                </div>
              )}
              <div className="mt-4">
                <div>Filter by</div>
                <div className="flex flex-row items-center gap-3">
                  <select
                    onChange={(e) =>
                      this.setState({ selected_level_code: e.target.value })
                    }
                    value={
                      this.state.selected_level_code === null
                        ? ""
                        : this.state.selected_level_code
                    }
                    className="border border-gray-400 rounded px-3 py-2"
                  >
                    <option value="">Choose education level</option>
                    <option value="">Filter by all levels</option>
                    {this.props.schoolHierarchyStore.hierarchy !== null &&
                      FC_GetSchoolLevels(
                        this.props.schoolHierarchyStore.hierarchy,
                        {
                          type: HierarchyDataType.SCHOOL_LEVELS,
                          status: true,
                        }
                      ).levels.map((item, i) => (
                        <option key={i + 1} value={item.level_code}>
                          {item.level_name}
                        </option>
                      ))}
                  </select>
                  <div>
                    <div
                      onClick={() =>
                        this.setState({
                          filter_by_combination:
                            !this.state.filter_by_combination,
                        })
                      }
                      className="w-auto border border-gray-400 rounded px-4 py-2 bg-white flex flex-row items-center justify-between gap-10 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                    >
                      {this.state.selected_combination_code === null ? (
                        <span>All- Click to filter by combination</span>
                      ) : (
                        <span>
                          {this.props.schoolHierarchyStore.hierarchy !== null &&
                            FC_GetSchoolLevels(
                              this.props.schoolHierarchyStore.hierarchy,
                              {
                                type: HierarchyDataType.COMBINATIONS,
                                status: true,
                              }
                            ).combinations.find(
                              (itm) =>
                                itm.combination_code ===
                                this.state.selected_combination_code
                            )?.combination_name}
                        </span>
                      )}
                      <div className="-mr-3">
                        {this.state.filter_by_combination === true ? (
                          <MdClose className="text-xl" />
                        ) : (
                          <IoIosArrowDown />
                        )}
                      </div>
                    </div>
                    {this.state.filter_by_combination === true && (
                      <div className="absolute w-max bg-white px-3 py-3 rounded-b animate__animated animate__zoomIn animate__faster shadow-xl">
                        <div>
                          <div className="flex flex-row items-center w-full justify-between gap-2">
                            <input
                              value={this.state.search_comb}
                              onChange={(e) =>
                                this.setState({ search_comb: e.target.value })
                              }
                              placeholder="Search..."
                              type="text"
                              className="border border-gray-400 rounded px-3 py-2 text-sm w-full"
                            />
                            <div>
                              <div
                                onClick={() =>
                                  this.setState({
                                    filter_by_combination: false,
                                  })
                                }
                                className="bg-red-100 text-red-800 text-2xl p-1 rounded-full cursor-pointer hover:bg-red-600 hover:text-white"
                              >
                                <MdClose />
                              </div>
                            </div>
                          </div>

                          {this.state.selected_combination_code !== null && (
                            <div
                              onClick={() =>
                                this.setState({
                                  selected_combination_code: null,
                                  filter_by_combination: false,
                                })
                              }
                              className="flex flex-row items-center gap-3 text-gray-800 font-bold cursor-pointer hover:text-accent-900 px-3 py-1 my-2 rounded bg-gray-100 w-full"
                            >
                              <div>
                                <GrPowerReset className="text-xl" />
                              </div>
                              <span>Click to view all</span>
                            </div>
                          )}
                        </div>
                        <div
                          className="h-64 overflow-y-auto mt-1 animate__animated animate__fadeIn"
                          style={{ minWidth: "28rem" }}
                        >
                          {this.props.schoolHierarchyStore.hierarchy !== null &&
                          search(
                            FC_GetSchoolLevels(
                              this.props.schoolHierarchyStore.hierarchy,
                              {
                                type: HierarchyDataType.COMBINATIONS,
                                status: true,
                              }
                            ).combinations,
                            this.state.search_comb
                          ).length === 0 ? (
                            <div className="text-center text-lg font-bold p-4 px-10">
                              No result found!
                            </div>
                          ) : (
                            this.props.schoolHierarchyStore.hierarchy !==
                              null &&
                            search(
                              FC_GetSchoolLevels(
                                this.props.schoolHierarchyStore.hierarchy,
                                {
                                  type: HierarchyDataType.COMBINATIONS,
                                  status: true,
                                }
                              ).combinations,
                              this.state.search_comb
                            ).map((item: FilteredCombination, i: number) => (
                              <div
                                key={i + 1}
                                className={`w-full flex flex-row items-center border-b ${
                                  this.state.selected_combination_code ===
                                  item.combination_code
                                    ? "border-yellow-100 bg-yellow-100 font-bold"
                                    : "border-gray-200"
                                } py-1 gap-4 hover:bg-yellow-100 cursor-pointer`}
                                onClick={() =>
                                  this.setState({
                                    selected_combination_code:
                                      item.combination_code,
                                    filter_by_combination: false,
                                  })
                                }
                              >
                                <div>
                                  <div className="rounded-full bg-yellow-50 border border-yellow-200 h-8 w-8 flex flex-row items-center justify-center font-bold">
                                    {i + 1}
                                  </div>
                                </div>
                                <div>{item.combination_name}</div>
                              </div>
                            ))
                          )}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
            <div className="bg-white rounded mx-2 border border-gray-200">
              <div className="w-full overflow-x-auto">
                <table className="min-w-full text-left">
                  <thead>
                    <tr className="font-semibold border-b text-base">
                      <th className="p-2 border-b-2 border-gray-300 text-center">
                        #
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">
                        Subject
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">Level</th>
                      <th className="p-2 border-b-2 border-gray-300">
                        Combination
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">Grade</th>
                      <th className="p-2 border-b-2 border-gray-300">
                        Periods
                      </th>
                      <th className="p-2 border-b-2 border-gray-300">Group</th>
                      {this.props.allowEdit && (
                        <th className="p-2 border-b-2 border-gray-300"></th>
                      )}
                    </tr>
                  </thead>
                  <tbody className="text-sm">
                    {this.state.FilteredData === null ? (
                      <tr>
                        <td colSpan={7}>
                          <GetLoading />
                        </td>
                      </tr>
                    ) : this.state.FilteredData.length === 0 ? (
                      <tr>
                        <td
                          colSpan={7}
                          className="text-center p-3 text-xl text-gray-400"
                        >
                          <div>No result found</div>
                        </td>
                      </tr>
                    ) : (
                      this.state.FilteredData.map((item, i) => (
                        <tr key={i + 1} className="hover:bg-yellow-50 group">
                          <td className="p-2 border-b text-center">{i + 1}</td>
                          <td className="p-2 border-b">
                            {item.course_code}-{item.course_name}
                          </td>
                          <td className="p-2 border-b">{item.level_name}</td>
                          <td className="p-2 border-b">
                            {item.combination_name}
                          </td>
                          <td className="p-2 border-b">{item.grade_name}</td>
                          <td className="p-2 border-b">
                            {item.teaching_period}
                          </td>
                          <td className="p-2 border-b uppercase">
                            <div className="flex flex-row gap-1 items-center">
                              {this.ReturnGradeGroups(item).map(
                                (gradeItem, i) => (
                                  <div
                                    className="bg-blue-100 rounded-md px-3 py-1 text-blue-800 w-max"
                                    key={i + 1}
                                  >
                                    {gradeItem}
                                  </div>
                                )
                              )}
                            </div>
                          </td>
                          {this.props.allowEdit && (
                            <td className="border-b">
                              <div className="flex justify-end">
                                <div
                                  onClick={() => {
                                    if (
                                      this.state.teacher_hierarchy_data !== null
                                    ) {
                                      let testId =
                                        this.state.teacherSchoolHierarchy.find(
                                          (itm) =>
                                            itm.studyhierarchyid ===
                                            item.studyhierarchyid
                                        );
                                      if (testId !== undefined) {
                                        this.RemoveTeacherSchoolHierarchy(
                                          testId.teacher_study_hierarchy_id
                                        );
                                      } else {
                                        alert("Plase try again!");
                                      }
                                    } else {
                                      alert("Plase try again!");
                                    }
                                  }}
                                  className="bg-red-50 text-red-800 px-3 py-2 mx-1 my-1 rounded font-bold group-hover:bg-red-700 group-hover:text-white cursor-pointer"
                                >
                                  Remove
                                </div>
                              </div>
                            </td>
                          )}
                        </tr>
                      ))
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>

        {/* Modal here */}
        {this.state.addNew === true &&
          this.props.auth.school !== null &&
          this.props.schoolHierarchyStore.hierarchy !== null &&
          this.state.school_hierarchy !== null && (
            <AddNewTeacherStudyHierarchy
              auth={this.props.auth}
              staff_code={this.props.staff_code}
              full_name={this.props.full_name}
              school={this.props.auth.school}
              setAddNew={this.setAddNew}
              allHierarchy={this.props.schoolHierarchyStore.hierarchy}
              teacher_hierarchy_data={this.FilterSchoolHierarchy(
                this.state.school_hierarchy
              )}
              teacher_data={this.state.teacherSchoolHierarchy}
              setLoadTeacherSubjects={this.setLoadTeacherSubjects}
            />
          )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  schoolHierarchyStore,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  schoolHierarchyStore: SchoolHierarchyStore;
} => {
  return {
    auth: auth,
    basic: basic,
    schoolHierarchyStore: schoolHierarchyStore,
  };
};

export const TeacherStudyHierarchy = connect(mapStateToProps, {
  FC_AllSchoolHierarchyDetails,
})(_TeacherStudyHierarchy);
