import axios from "axios";
import React, { useState } from "react";
import { GrPowerReset } from "react-icons/gr";
import { IoIosArrowDown } from "react-icons/io";
import { MdAdd, MdClose } from "react-icons/md";
import { RiTodoLine } from "react-icons/ri";
import { ProfileType } from "../../types/userInfo";
import {
  AuthSchool,
  FC_GetSchoolLevels,
  FilteredCombination,
  HierarchyDataType,
  SchoolHierarchyInterface,
  Auth,
  TeacherHierarchyGet,
} from "../../actions";
import { APP_URL } from "../../config/app.config";
import { search } from "../../utils/functions";
import { GetLoading } from "./TeacherStudyHierarchy";
import StudyGroups from "./StudyGroups";

export const SubmitTeacherSubjects = async (
  subjects: SchoolHierarchyInterface[],
  addedGroupsList: {
    study_hierarchy_id: string;
    group: string[];
  }[],
  teacher_code: string,
  callBack: (status: boolean, msg: string) => void
) => {
  if (subjects.length === 0) {
    return alert("Please select atleast one subject");
  }
  let newSubjects: {
    teacher_code: string;
    study_hierarchy_id: string;
    grade_group: string;
  }[] = subjects.map((item) => ({
    teacher_code: teacher_code,
    study_hierarchy_id: item.studyhierarchyid,
    grade_group:
      addedGroupsList.find(
        (itm) => itm.study_hierarchy_id === item.studyhierarchyid
      ) === undefined
        ? "A"
        : JSON.stringify(
            addedGroupsList.find(
              (itm) => itm.study_hierarchy_id === item.studyhierarchyid
            )!.group
          ),
  }));
  callBack(false, "");
  console.log("Sent data: ", newSubjects);
  try {
    const res = await axios.post<
      {
        teacher_code: string;
        study_hierarchy_id: string;
        grade_group: string;
      }[]
    >(`${APP_URL}/teacherhierarch/create`, newSubjects);
    callBack(true, "Teacher subjects has added successfully!");
    console.log("Success: ", res);
  } catch (error: any) {
    callBack(true, "");
    console.log("Err: ", { ...error });
  }
};

interface AddNewProps {
  setAddNew: (status: boolean) => void;
  school: AuthSchool;
  allHierarchy: SchoolHierarchyInterface[];
  teacher_hierarchy_data: SchoolHierarchyInterface[] | null;
  setLoadTeacherSubjects: () => void;
  auth: Auth;
  staff_code: string;
  full_name: string;
  teacher_data: TeacherHierarchyGet[];
}

const AddNewTeacherStudyHierarchy = (props: AddNewProps) => {
  const [selected_level_code, setSelected_level_code] = useState<string | null>(
    null
  );
  const [filter_by_combination, setFilter_by_combination] =
    useState<boolean>(false);
  const [selected_combination_code, setSelected_combination_code] = useState<
    string | null
  >(null);
  const [search_comb, setSearch_comb] = useState<string>("");
  const [showStudyGroups, setShowStudyGroups] = useState<string | null>(null);
  const [addedSubjects, setAddedSubjects] = useState<
    SchoolHierarchyInterface[]
  >([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [success, setSuccess] = useState<string>("");
  const [error, setError] = useState<string>("");
  const [addedSubjectsGroups, setAddedSubjectsGroups] = useState<
    {
      study_hierarchy_id: string;
      group: string[];
    }[]
  >([]);

  const addSubjectGroup = (study_hierarchy_id: string, group: string[]) => {
    console.log(group);
    setAddedSubjectsGroups([
      ...addedSubjectsGroups.filter(
        (itm) => itm.study_hierarchy_id !== study_hierarchy_id
      ),
      {
        study_hierarchy_id: study_hierarchy_id,
        group: group,
      },
    ]);
  };

  const SelectSubject = (subject: SchoolHierarchyInterface) => {
    setAddedSubjects([
      ...addedSubjects.filter(
        (itm) => itm.studyhierarchyid !== subject.studyhierarchyid
      ),
      subject,
    ]);
  };

  const RemoveSelectedSubject = (subject: SchoolHierarchyInterface) => {
    setAddedSubjects([
      ...addedSubjects.filter(
        (itm) => itm.studyhierarchyid !== subject.studyhierarchyid
      ),
    ]);
  };

  const FilteredData = (): SchoolHierarchyInterface[] => {
    let res = props.teacher_hierarchy_data;
    if (res !== null) {
      if (selected_level_code !== null && selected_level_code !== "") {
        res = res.filter((itm) => itm.level_code === selected_level_code);
      }
      if (selected_combination_code !== null) {
        res = res.filter(
          (itm) => itm.combination_code === selected_combination_code
        );
      }
      return res;
    } else {
      return [];
    }
  };

  const ReturnGradeGroups = (item: SchoolHierarchyInterface): string[] => {
    let test =
      props.teacher_hierarchy_data === null
        ? undefined
        : addedSubjectsGroups.find(
            (itm) => itm.study_hierarchy_id === item.studyhierarchyid
          )?.group;
    if (test !== undefined) {
      return test;
    } else {
      let newTest =
        props.teacher_data === null || props.teacher_data === undefined
          ? undefined
          : props.teacher_data.find(
              (itm) => itm.study_hierarchy_id === item.studyhierarchyid
            )?.grade_group;
      if (newTest !== undefined) {
        return JSON.parse(newTest);
      }
    }
    return [];
  };

  const TotalPeriods = (): number => {
    let total = 0;
    // For new data
    for (let item of addedSubjects) {
      let test = addedSubjectsGroups.find(
        (itm) => itm.study_hierarchy_id === item.studyhierarchyid
      );
      if (test !== undefined) {
        total += parseInt(item.teaching_period) * test.group.length;
      }
    }
    // For the existing
    if (
      props.teacher_hierarchy_data !== null &&
      props.teacher_hierarchy_data.length > 0
    ) {
      for (let item of props.teacher_hierarchy_data.filter(
        (itm) =>
          addedSubjects.find(
            (tes) => tes.studyhierarchyid === itm.studyhierarchyid
          ) === undefined
      )) {
        total +=
          parseInt(item.teaching_period) * ReturnGradeGroups(item).length;
      }
    }
    return total;
  };

  return (
    <div className="fixed bg-black bg-opacity-50 top-0 left-0 w-full h-full z-50 flex items-center justify-center animate__animated animate__fadeIn animate__faster">
      <div className="bg-white rounded-md animate__animated animate__zoomIn animate__faster z-50 w-full h-full overflow-auto">
        <div className="rounded-t-md flex flex-row items-center justify-between gap-4 px-2 py-2 border-b border-gray-400">
          <div className="text-xl font-bold ml-2 uppercase mt-2 text-primary-800">
            TEACHER SUBJECTS ASSIGNMENT
          </div>
          <div
            onClick={() => props.setAddNew(false)}
            className="flex items-center justify-center p-1 rounded-full bg-accent-900 text-3xl hover:bg-yellow-900 text-white cursor-pointer border border-accent-900 hover:border-yellow-900"
            title="Close window"
          >
            <MdClose />
          </div>
        </div>
        <div className="p-2 md:p-6">
          <div className="flex flex-row items-center justify-between gap-3">
            <div className="flex flex-row items-center gap-3">
              <div className="text-5xl text-gray-400">
                <RiTodoLine />
              </div>
              <div className="flex flex-col">
                <span className="font-bold -mt-1">
                  Teacher: {props.full_name}
                </span>
                <span className="font-bold -mt-1">
                  School name: {props.school.school_name} (
                  {props.school.school_code})
                </span>
              </div>
            </div>

            <div
              className={`flex flex-col justify-center items-end ${
                TotalPeriods() > 45 ? "bg-red-100 text-red-800 rounded p-3" : ""
              }`}
            >
              <span className="text-gray-500">
                Total periods {TotalPeriods() > 45 ? "(overloaded)" : ""}
              </span>
              <span className="font-extrabold text-3xl">{TotalPeriods()}</span>
            </div>
          </div>
          <div className="">
            <div className="mb-10">
              <div>
                <div className="p-2 mb-5">
                  <div className="mt-4">
                    <div>Filter by</div>
                    <div className="flex flex-row items-center gap-3">
                      <select
                        onChange={(e) => setSelected_level_code(e.target.value)}
                        value={
                          selected_level_code === null
                            ? ""
                            : selected_level_code
                        }
                        className="border border-gray-400 rounded px-3 py-2"
                      >
                        <option value="">Choose education level</option>
                        <option value="">Show all levels</option>
                        {FC_GetSchoolLevels(props.allHierarchy, {
                          type: HierarchyDataType.SCHOOL_LEVELS,
                          status: true,
                        }).levels.map((item, i) => (
                          <option key={i + 1} value={item.level_code}>
                            {item.level_name}
                          </option>
                        ))}
                      </select>
                      <div>
                        <div
                          onClick={() =>
                            setFilter_by_combination(!filter_by_combination)
                          }
                          className="w-auto border border-gray-400 rounded px-4 py-2 bg-white flex flex-row items-center justify-between gap-10 cursor-pointer hover:border-accent-900 hover:text-accent-900"
                        >
                          {selected_combination_code === null ? (
                            <span>All- Click to filter by combination</span>
                          ) : (
                            <span>
                              {
                                FC_GetSchoolLevels(props.allHierarchy, {
                                  type: HierarchyDataType.COMBINATIONS,
                                  status: true,
                                }).combinations.find(
                                  (itm) =>
                                    itm.combination_code ===
                                    selected_combination_code
                                )?.combination_name
                              }
                            </span>
                          )}
                          <div className="-mr-3">
                            {filter_by_combination === true ? (
                              <MdClose />
                            ) : (
                              <IoIosArrowDown />
                            )}
                          </div>
                        </div>
                        {filter_by_combination === true && (
                          <div className="absolute w-max bg-white px-3 py-3 rounded-b animate__animated animate__zoomIn animate__faster shadow-xl">
                            <div>
                              <div className="flex flex-row items-center w-full justify-between gap-2">
                                <input
                                  value={search_comb}
                                  onChange={(e) =>
                                    setSearch_comb(e.target.value)
                                  }
                                  placeholder="Search..."
                                  type="text"
                                  className="border border-gray-400 rounded px-3 py-2 text-sm w-full"
                                />
                                <div>
                                  <div
                                    onClick={() =>
                                      setFilter_by_combination(false)
                                    }
                                    className="bg-red-100 text-red-800 text-2xl p-1 rounded-full cursor-pointer hover:bg-red-600 hover:text-white"
                                  >
                                    <MdClose />
                                  </div>
                                </div>
                              </div>
                              {selected_combination_code !== null && (
                                <div
                                  onClick={() => {
                                    setSelected_combination_code(null);
                                    setFilter_by_combination(false);
                                  }}
                                  className="flex flex-row items-center gap-3 text-gray-800 font-bold cursor-pointer hover:text-accent-900 px-3 py-1 my-2 rounded bg-gray-100 w-full"
                                >
                                  <div>
                                    <GrPowerReset className="text-xl" />
                                  </div>
                                  <span>Click to view all</span>
                                </div>
                              )}
                            </div>
                            <div
                              className="h-64 overflow-y-auto mt-1 animate__animated animate__fadeIn"
                              style={{ minWidth: "28rem" }}
                            >
                              {search(
                                FC_GetSchoolLevels(props.allHierarchy, {
                                  type: HierarchyDataType.COMBINATIONS,
                                  status: true,
                                }).combinations,
                                search_comb
                              ).length === 0 ? (
                                <div className="text-center text-lg font-bold p-4 px-10">
                                  No result found!
                                </div>
                              ) : (
                                search(
                                  FC_GetSchoolLevels(props.allHierarchy, {
                                    type: HierarchyDataType.COMBINATIONS,
                                    status: true,
                                  }).combinations,
                                  search_comb
                                ).map(
                                  (item: FilteredCombination, i: number) => (
                                    <div
                                      key={i + 1}
                                      className={`w-full flex flex-row items-center border-b ${
                                        selected_combination_code ===
                                        item.combination_code
                                          ? "border-yellow-100 bg-yellow-100 font-bold"
                                          : "border-gray-200"
                                      } py-1 gap-4 hover:bg-yellow-100 cursor-pointer`}
                                      onClick={() => {
                                        setSelected_combination_code(
                                          item.combination_code
                                        );
                                        setFilter_by_combination(false);
                                      }}
                                    >
                                      <div>
                                        <div className="rounded-full bg-yellow-50 border border-yellow-200 h-8 w-8 flex flex-row items-center justify-center font-bold">
                                          {i + 1}
                                        </div>
                                      </div>
                                      <div>{item.combination_name}</div>
                                    </div>
                                  )
                                )
                              )}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
                <div className="bg-white rounded mx-2 border border-gray-200">
                  <div className="w-full overflow-x-auto">
                    <table className="min-w-full text-left">
                      <thead>
                        <tr className="font-semibold border-b text-base">
                          <th className="p-2 border-b-2 border-gray-300 text-center">
                            #
                          </th>
                          <th className="p-2 border-b-2 border-gray-300">
                            Subject
                          </th>
                          <th className="p-2 border-b-2 border-gray-300">
                            Level
                          </th>
                          <th className="p-2 border-b-2 border-gray-300">
                            Combination
                          </th>
                          <th className="p-2 border-b-2 border-gray-300">
                            Grade
                          </th>
                          <th className="p-2 border-b-2 border-gray-300 text-center">
                            Periods
                          </th>
                          <th className="p-2 border-b-2 border-gray-300 text-right">
                            Action
                          </th>
                        </tr>
                      </thead>
                      <tbody className="text-sm">
                        {props.teacher_hierarchy_data === null ? (
                          <tr>
                            <td colSpan={7}>
                              <GetLoading />
                            </td>
                          </tr>
                        ) : FilteredData().length === 0 ? (
                          <td
                            colSpan={7}
                            className="text-center p-3 text-xl text-gray-400"
                          >
                            <div>No result found</div>
                          </td>
                        ) : (
                          FilteredData().map((item, i) => (
                            <tr
                              key={i + 1}
                              className={`${
                                addedSubjects.find(
                                  (itm) =>
                                    itm.studyhierarchyid ===
                                    item.studyhierarchyid
                                ) !== undefined
                                  ? "text-gray-500"
                                  : props.teacher_data === null ||
                                    props.teacher_data.find(
                                      (itm) =>
                                        itm.studyhierarchyid ===
                                        item.studyhierarchyid
                                    ) === undefined
                                  ? "hover:bg-yellow-50 hover:text-accent-900"
                                  : "hover:bg-blue-50 hover:text-primary-800"
                              } group`}
                            >
                              <td className="p-2 border-b text-center">
                                {i + 1}
                              </td>
                              <td className="p-2 border-b">
                                {item.course_code}-{item.course_name}
                              </td>
                              <td className="p-2 border-b">
                                {item.level_name}
                              </td>
                              <td className="p-2 border-b">
                                {item.combination_name}
                              </td>
                              <td className="p-2 border-b">
                                {item.grade_name}
                              </td>
                              <td className="p-2 border-b text-center">
                                {item.teaching_period}
                              </td>
                              <td className="p-2 border-b">
                                <div className="flex flex-row items-center justify-end gap-2">
                                  <div className="mx-3">
                                    {addedSubjects.find(
                                      (itm) =>
                                        itm.studyhierarchyid ===
                                        item.studyhierarchyid
                                    ) === undefined ? (
                                      <div className="flex flex-row items-center justify-end gap-1">
                                        <div className="flex flex-row items-center gap-1 justify-end">
                                          {ReturnGradeGroups(item).map(
                                            (test, t) => (
                                              <div
                                                className="bg-blue-100 rounded-md px-2 uppercase text-primary-800"
                                                key={t + 1}
                                              >
                                                {test}
                                              </div>
                                            )
                                          )}
                                        </div>
                                        <div
                                          onClick={() => {
                                            let test =
                                              props.teacher_data === null
                                                ? undefined
                                                : props.teacher_data.find(
                                                    (itm) =>
                                                      itm.studyhierarchyid ===
                                                      item.studyhierarchyid
                                                  );
                                            (showStudyGroups === null ||
                                              showStudyGroups ===
                                                item.studyhierarchyid) &&
                                              setShowStudyGroups(
                                                item.studyhierarchyid
                                              );
                                            addSubjectGroup(
                                              item.studyhierarchyid,
                                              props.teacher_hierarchy_data ===
                                                null || test === undefined
                                                ? []
                                                : JSON.parse(test.grade_group)
                                            );
                                          }}
                                          className={`rounded ${
                                            props.teacher_data === null ||
                                            props.teacher_data.find(
                                              (itm) =>
                                                itm.studyhierarchyid ===
                                                item.studyhierarchyid
                                            ) === undefined
                                              ? "bg-yellow-100 group-hover:bg-accent-900 border-yellow-300 group-hover:border-accent-900"
                                              : "bg-blue-100 group-hover:bg-primary-800 border-blue-300 group-hover:border-primary-800"
                                          }   group-hover:text-white px-3 py-2 w-max text-center border cursor-pointer text-sm font-bold -mr-3`}
                                        >
                                          {props.teacher_data === null ||
                                          props.teacher_data.find(
                                            (itm) =>
                                              itm.studyhierarchyid ===
                                              item.studyhierarchyid
                                          ) === undefined
                                            ? "Add subject"
                                            : "Update subj"}
                                        </div>
                                      </div>
                                    ) : (
                                      <div className="flex flex-row items-center gap-1 justify-end -mr-3">
                                        <div className="flex flex-row items-center gap-1 justify-end">
                                          {ReturnGradeGroups(item).map(
                                            (test, t) => (
                                              <div
                                                className="bg-blue-100 rounded-md px-2 uppercase text-primary-800"
                                                key={t + 1}
                                              >
                                                {test}
                                              </div>
                                            )
                                          )}
                                        </div>
                                        <div
                                          onClick={() =>
                                            RemoveSelectedSubject(item)
                                          }
                                          className="rounded bg-red-50 group-hover:bg-red-200 text-black border border-red-100 group-hover:border-red-700 group-hover:text-red-800 px-3 py-2 w-max text-center cursor-pointer text-sm font-bold"
                                        >
                                          Remove
                                        </div>
                                      </div>
                                    )}
                                    {showStudyGroups !== null &&
                                      showStudyGroups ===
                                        item.studyhierarchyid && (
                                        <StudyGroups
                                          addSubjectGroup={(
                                            study_hierarchy_id: string,
                                            group: string[]
                                          ) => {
                                            addSubjectGroup(
                                              study_hierarchy_id,
                                              group
                                            );
                                            console.log(
                                              "Test grp: ",
                                              addedSubjectsGroups
                                            );

                                            SelectSubject(item);
                                            setShowStudyGroups(null);
                                          }}
                                          addedGroups={addedSubjectsGroups.find(
                                            (itm) =>
                                              itm.study_hierarchy_id ===
                                              item.studyhierarchyid
                                          )}
                                          cancel={() =>
                                            setShowStudyGroups(null)
                                          }
                                        />
                                      )}
                                  </div>
                                </div>
                              </td>
                            </tr>
                          ))
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {addedSubjects.length > 0 && (
        <div className="fixed bottom-0 left-0 w-full z-50 bg-blue-600 p-2 animate__animated animate__fadeInUp">
          {loading === false ? (
            <div className="flex flex-row items-center justify-end gap-2">
              {error !== "" && (
                <div
                  className="bg-red-200 text-red-800 px-3 py-2 rounded-md animate_animated animate__zoomIn animate__faster"
                  onClick={() => setError("")}
                >
                  {error}
                </div>
              )}
              {success !== "" && (
                <div
                  className="bg-green-600 text-white px-3 py-2 rounded-md animate_animated animate__zoomIn animate__faster"
                  onClick={() => setSuccess("")}
                >
                  {success}
                </div>
              )}
              <div className="text-white flex flex-col items-end mr-2">
                <span className="text-sm text-blue-300">Selected subjects</span>
                <span className="text-2xl font-extrabold -mt-2">
                  {addedSubjects.length}
                </span>
              </div>
              <div
                onClick={() => props.setAddNew(false)}
                className="btn bg-gray-200 text-black rounded-md px-4 py-2 hover:bg-gray-700 hover:text-white cursor-pointer border-2 border-gray-200 hover:border-white font-bold"
              >
                Cancel
              </div>
              <div
                onClick={() => {
                  setLoading(true);
                  props.auth.user !== null &&
                    SubmitTeacherSubjects(
                      addedSubjects,
                      addedSubjectsGroups,
                      props.staff_code,
                      (status: boolean, msg: string) => {
                        if (msg !== "" && status === true) {
                          setSuccess("School subjects added successfully!");
                          setAddedSubjects([]);
                          props.setAddNew(false);
                          props.setLoadTeacherSubjects();
                        }
                        if (msg === "" && status === true) {
                          setError("Something went wrong. Try again!");
                        }
                        setLoading(false);
                      }
                    );
                  setLoading(false);
                }}
                className="btn bg-green-700 text-white rounded-md px-4 py-2 hover:bg-gray-700 hover:text-white cursor-pointer border-2 border-gray-200 hover:border-white font-bold"
              >
                Save changes
              </div>
            </div>
          ) : (
            <div className="text-white text-lg text-right py-2">
              Saving, Please wait...
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default AddNewTeacherStudyHierarchy;
