import React, { useState } from "react";
import { MdClose } from "react-icons/md";

interface StudyGroupsProps {
  addedGroups:
    | {
        study_hierarchy_id: string;
        group: string[];
      }
    | undefined;
  addSubjectGroup: (study_hierarchy_id: string, group: string[]) => void;
  cancel: () => void;
}

const StudyGroups = (props: StudyGroupsProps) => {
  const [newGroups, setNewGroups] = useState<string[]>(
    props.addedGroups === undefined ? [] : props.addedGroups.group
  );
  const [group, setGroup] = useState<string>("");
  const [error, setError] = useState<string>("");
  return (
    <div
      className="absolute bg-primary-800 p-2 rounded-md shadow-xl right-2 md:right-10 animate__animated animate__zoomIn animate__faster mb-10"
      style={{ minWidth: "300px" }}
    >
      <div className="text-lg text-white mb-2 text-left px-2 font-bold">
        ADD GROUPS
      </div>
      <div>
        {newGroups.length === 0 ? (
          <div className="border-2 border-blue-500 text-blue-400 rounded-md px-3 py-4 mb-1 text-center text-base">
            No groups
          </div>
        ) : (
          newGroups.map((item, i) => (
            <div
              key={i + 1}
              className="flex flex-row items-center justify-between gap-2 border-2 border-blue-300 rounded-md pl-3 py-1 pr-1 text-white font-bold mb-1"
            >
              <div className="uppercase">{item}</div>
              <div
                onClick={() =>
                  setNewGroups([...newGroups.filter((itm) => itm !== item)])
                }
                className="bg-red-100 rounded-full text-lg p-1 text-red-700 cursor-pointer"
              >
                <MdClose />
              </div>
            </div>
          ))
        )}
      </div>
      <div className="flex flex-row items-center justify-end gap-2 mt-2">
        <input
          value={group}
          onChange={(e) => {
            setError("");
            setGroup(e.target.value);
          }}
          placeholder="Group name"
          className={`border-0 px-3 py-2 rounded w-full`}
        />
        {group !== "" && (
          <div
            onClick={() => {
              if (group === "") {
                setError("Please fill group name!");
              } else {
                setError("");
                setNewGroups([
                  ...newGroups.filter((itm) => itm !== group),
                  group,
                ]);
                setGroup("");
              }
            }}
            className="rounded px-3 py-2 font-bold  cursor-pointer bg-blue-500 hover:bg-blue-600 text-white w-36 animate__animated animate__fadeIn animate__faster"
          >
            Add group
          </div>
        )}
      </div>
      {error !== "" && (
        <div
          className="bg-red-100 text-red-700 px-3 py-2 rounded my-1 animate__animated animate__zoomIn animate__faster"
          onClick={() => setError("")}
        >
          {error}
        </div>
      )}
      {
        <div className="flex flex-row items-center justify-end gap-2 mt-3 animate__animated animate__fadeInUp animate__faster">
          <div
            onClick={() => props.cancel()}
            className="rounded px-3 py-2 font-bold  cursor-pointer bg-gray-400 hover:bg-gray-500 text-white"
          >
            Cancel
          </div>
          {newGroups.length > 0 && (
            <div
              onClick={() => {
                console.log("sd: ", props.addedGroups);
                props.addedGroups !== undefined &&
                  props.addSubjectGroup(
                    props.addedGroups.study_hierarchy_id,
                    newGroups
                  );
              }}
              className="rounded px-3 py-2 font-bold  cursor-pointer bg-green-500 hover:bg-green-600 text-white animate__animated animate__fadeInRight animate__faster"
            >
              Save changes
            </div>
          )}
        </div>
      }
    </div>
  );
};

export default StudyGroups;
